import { useState, useEffect } from 'react';
import { Link, useLocation } from 'wouter';
import { motion, AnimatePresence } from 'framer-motion';

interface NavBarProps {
  scrollY: number;
}

const NavBar = ({ scrollY }: NavBarProps) => {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [location] = useLocation();
  const [isScrolled, setIsScrolled] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);
  
  // Calculate blur intensity based on scroll
  const getBlurIntensity = () => {
    const minBlur = 5;
    const maxBlur = 10;
    const scrollRange = 100;
    
    const blur = minBlur + Math.min(1, scrollY / scrollRange) * (maxBlur - minBlur);
    return `blur(${blur}px)`;
  };
  
  // Animation variants for elements
  const logoVariants = {
    hidden: { opacity: 0, scale: 0.8 },
    visible: { 
      opacity: 1, 
      scale: 1,
      transition: {
        type: "spring",
        stiffness: 260,
        damping: 20,
        delay: 0.1
      }
    }
  };
  
  const navItemVariants = {
    hidden: { opacity: 0, y: -10 },
    visible: (custom: number) => ({ 
      opacity: 1, 
      y: 0,
      transition: {
        type: "spring",
        stiffness: 300,
        damping: 24,
        delay: 0.15 + custom * 0.05
      }
    })
  };
  
  const buttonVariants = {
    hidden: { opacity: 0, scale: 0.9 },
    visible: (custom: number) => ({ 
      opacity: 1, 
      scale: 1,
      transition: {
        type: "spring",
        stiffness: 260,
        damping: 20,
        delay: 0.3 + custom * 0.07
      }
    })
  };
  
  // Update scroll state for enhanced styling
  useEffect(() => {
    setIsScrolled(scrollY > 100);
  }, [scrollY]);
  
  // Set loaded state after mount for animations
  useEffect(() => {
    const timer = setTimeout(() => {
      setIsLoaded(true);
    }, 100);
    return () => clearTimeout(timer);
  }, []);
  
  const isActive = (path: string) => location === path;
  
  // Helper function to get nav item style based on active state
  const getNavItemStyle = (path: string) => {
    const baseStyle = "font-medium transition-all duration-300 px-4 py-2 rounded-full cursor-pointer";
    const activeStyle = "bg-white/15 text-white backdrop-blur-sm shadow-inner";
    const inactiveStyle = "text-white/80 hover:bg-white/10 hover:text-white";
    
    return `${baseStyle} ${isActive(path) ? activeStyle : inactiveStyle}`;
  };
  
  // Navigation items data
  const navigationItems = [
    { path: "/", label: "Home", icon: "fa-home" },
    { path: "/listings", label: "Listings", icon: "fa-building" },
    { path: "/map", label: "Map", icon: "fa-map-marker-alt" },
    { path: "/resources", label: "Resources", icon: "fa-book" },
    { path: "/about", label: "About", icon: "fa-info-circle" }
  ];

  return (
    <header className="fixed top-0 left-0 right-0 z-50">
      <motion.nav 
        className={`transition-all duration-500 overflow-hidden mx-4 lg:mx-8 my-4 rounded-2xl`}
        style={{
          backdropFilter: getBlurIntensity()
        }}
      >
        {/* Dynamic navbar background */}
        <div 
          className={`absolute inset-0 transition-all duration-500 rounded-2xl border border-white/10`}
          style={{
            background: `linear-gradient(to right, rgba(26, 32, 77, 0.8), rgba(59, 66, 113, 0.8))`,
            boxShadow: '0 10px 30px rgba(0, 0, 0, 0.25)'
          }}
        />
        
        {/* Animated glow effect */}
        <div className={`absolute inset-0 transition-opacity duration-500 ${isScrolled ? 'opacity-0' : 'opacity-100'} pointer-events-none`}>
          <div className="absolute -top-20 -right-20 w-64 h-64 bg-blue-500 rounded-full opacity-10 blur-3xl"></div>
          <div className="absolute -bottom-20 -left-20 w-64 h-64 bg-purple-500 rounded-full opacity-10 blur-3xl"></div>
        </div>
        
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16 items-center">
            {/* Logo */}
            <motion.div 
              className="flex items-center"
              variants={logoVariants}
              initial="hidden"
              animate={isLoaded ? "visible" : "hidden"}
            >
              <Link href="/">
                <div className="flex items-center gap-2.5 group">
                  <div className="relative">
                    <div className="absolute -inset-0.5 bg-gradient-to-r from-blue-500 to-purple-600 rounded-full blur opacity-60 group-hover:opacity-100 transition duration-300"></div>
                    <div className="relative w-10 h-10 bg-white rounded-full flex items-center justify-center shadow-lg">
                      <img src="/src/assets/logo.svg" alt="LetMeKnock Logo" className="w-9 h-9" />
                    </div>
                  </div>
                  <motion.span 
                    className="text-white font-bold text-xl tracking-tight"
                    initial={{ opacity: 0, x: -10 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: 0.2, duration: 0.4 }}
                  >
                    LetMeKnock
                  </motion.span>
                </div>
              </Link>
            </motion.div>
            
            {/* Desktop Navigation */}
            <div className="hidden md:flex items-center space-x-1">
              {navigationItems.map((item, index) => (
                <motion.div
                  key={item.path}
                  custom={index}
                  variants={navItemVariants}
                  initial="hidden"
                  animate={isLoaded ? "visible" : "hidden"}
                >
                  <Link href={item.path}>
                    <div className={getNavItemStyle(item.path)}>
                      <i className={`fas ${item.icon} mr-2`}></i> {item.label}
                    </div>
                  </Link>
                </motion.div>
              ))}
            </div>
            
            {/* Combined Sign In/Up Button & Mobile Menu Button */}
            <div className="flex items-center space-x-3">
              <motion.div
                variants={buttonVariants}
                custom={0}
                initial="hidden"
                animate={isLoaded ? "visible" : "hidden"}
                className="flex space-x-2"
              >
                <Link href="/profile">
                  <div className="relative">
                    <div className="absolute -inset-0.5 bg-gradient-to-r from-blue-400 to-primary-500 rounded-full blur opacity-50 group-hover:opacity-75 transition duration-300"></div>
                    <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center border border-primary-200 hover:bg-primary-50 transition-all duration-300 shadow-sm cursor-pointer text-primary-700 relative group">
                      <i className="fas fa-user-circle text-lg group-hover:scale-110 transition-transform duration-300"></i>
                      <div className="absolute top-0 right-0 w-2 h-2 bg-green-500 rounded-full border border-white transform scale-0 group-hover:scale-100 transition-transform duration-300"></div>
                    </div>
                  </div>
                </Link>
                
                <Link href="/signin">
                  <div className="relative group">
                    <div className="absolute -inset-0.5 bg-gradient-to-r from-blue-500 to-purple-600 rounded-full blur opacity-50 group-hover:opacity-75 transition duration-300"></div>
                    <div className="relative font-medium text-primary-800 bg-white hover:bg-gray-50 px-5 py-2 rounded-full transition-colors shadow-lg cursor-pointer flex items-center">
                      <i className="fas fa-sign-in-alt mr-2"></i> Sign In
                    </div>
                  </div>
                </Link>
              </motion.div>
              
              <motion.button 
                className="md:hidden text-white focus:outline-none relative z-20" 
                onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                whileTap={{ scale: 0.95 }}
                variants={buttonVariants}
                custom={1}
                initial="hidden"
                animate={isLoaded ? "visible" : "hidden"}
              >
                <div className="w-10 h-10 bg-white/10 backdrop-blur-sm rounded-full flex items-center justify-center border border-white/20">
                  <i className={`fas ${mobileMenuOpen ? 'fa-times' : 'fa-bars'} text-lg`}></i>
                </div>
              </motion.button>
            </div>
          </div>
        </div>
        
        {/* Mobile menu with improved animation */}
        <AnimatePresence>
          {mobileMenuOpen && (
            <motion.div 
              className="md:hidden relative z-10"
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: "auto" }}
              exit={{ opacity: 0, height: 0 }}
              transition={{ 
                opacity: { duration: 0.2 },
                height: { duration: 0.3 }
              }}
            >
              <motion.div 
                className="bg-gray-900/95 backdrop-blur-md p-4 mx-4 rounded-2xl shadow-2xl border border-white/10 my-2"
                initial={{ y: -20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ duration: 0.3, delay: 0.1 }}
              >
                <div className="flex flex-col space-y-1 divide-y divide-white/5">
                  {navigationItems.map((item, index) => (
                    <motion.div 
                      key={item.path}
                      initial={{ x: -20, opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ delay: 0.1 + index * 0.05, duration: 0.3 }}
                      className={index === 0 ? "" : "pt-1"}
                    >
                      <Link href={item.path}>
                        <div className={`font-medium ${
                          isActive(item.path) 
                            ? 'bg-blue-500/20 text-white border-l-2 border-blue-500' 
                            : 'text-white/70 border-l-2 border-transparent'} 
                            hover:bg-blue-500/10 hover:text-white px-4 py-3 rounded-xl cursor-pointer transition-all`
                        }>
                          <i className={`fas ${item.icon} mr-3 text-blue-400`}></i> {item.label}
                        </div>
                      </Link>
                    </motion.div>
                  ))}
                  
                  <div className="pt-4 pb-1">
                    <motion.div
                      initial={{ y: 10, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{ delay: 0.3, duration: 0.3 }}
                      className="w-full"
                    >
                      <Link href="/signin">
                        <div className="relative group">
                          <div className="absolute -inset-0.5 bg-gradient-to-r from-blue-500 to-purple-600 rounded-xl blur opacity-50 group-hover:opacity-75 transition duration-300"></div>
                          <div className="relative font-medium text-white bg-primary-700 hover:bg-primary-600 py-3 rounded-xl cursor-pointer transition-colors shadow w-full text-center flex items-center justify-center">
                            <i className="fas fa-sign-in-alt mr-2"></i> Sign In
                          </div>
                        </div>
                      </Link>
                    </motion.div>
                  </div>
                </div>
              </motion.div>
            </motion.div>
          )}
        </AnimatePresence>
      </motion.nav>
    </header>
  );
};

export default NavBar;
